.link {
  text-decoration: none;
  color: $color-white;
  font-size: 17px;
  display: flex;
  margin: 10px auto;
  span {
    margin-right: 10px;
  }
  .btn-more {
    transform: translateY(-5px);
    i.fa {
      font-size: 18px;
    }
  }
  &-dark {
    color: $color-base;
  }
}
